




















































import ut from '../../utils/index.js';
export default {
	data() {
		return {
			check:'-1',
			classlist:[],
			provinceName:'',
			countyAreaName:'',
			cityName:'',
			name:'',
			phone:'',
			adress:"",
			sexList:[{
				type:'男'
			},
			{
				type:'女'
			}],
			sex:'',
			type:''
		}
	},
	onLoad(opt) {
		this.type=opt.type;
		if(opt.type==1){
			ut.settitle('申请木斗工匠')
		}
		if(opt.type==2){
			ut.settitle('申请木斗商户')
		}
		if(opt.type==3){
			ut.settitle('申请木斗速递')
		}
		if(opt.type==4){
			ut.settitle('申请木斗搬运')
		}
		if(opt.type==5){
			ut.settitle('申请订制商户')
		}
		if(opt.type==6){
			ut.settitle('申请地区加盟')
		}
		this.req_class()
	},
	methods: {
		bindsexChange(e){
			this.sex=this.sexList[e.target.value[0]].type;
		},
		bindRegionChange: function (e) {
			if(e.target.value[2]!="通州区"){
				ut.totast('现在只开通通州的服务，其他区域敬请期待！');
				return
			}
			this.provinceName=e.target.value[0];
			this.countyAreaName=e.target.value[1];
			this.cityName=e.target.value[2];
		},
		cg_check(index){
			this.classlist[index].check=!this.classlist[index].check;
		},
		go_mine_infchange(){
			wx.navigateTo({
				url: '../mine/infchange'
			})
		},
		go_mine_apply(){
			wx.navigateTo({
				url: '../mine/apply'
			})
		},
		req_class(){
			let url = "service/class";
			if(this.type==6||this.type==4)return
			if(this.type==3){
				url='order/vehiclelist'
			}
			if(this.type==2||this.type==5){
				url='goods/class'
			}
			ut.request({
				data: {
					parentid:0
				},
				url: url
			}).then(data=>{
				if(data){
					data.forEach(item=>{
						item.check = false;
					})
					this.classlist=data;
				}
				
			})
		},
		submit(){
			let serviceClassIds;
			if(this.classlist.length){
				serviceClassIds=this.return_serviceClassIds();
			}
			if(!serviceClassIds&&(this.type==2||this.type==5||this.type==1)){
				ut.totast('请选择服务类型');
				return
			}
			if(!serviceClassIds&&this.type==3){
				ut.totast('请选择车辆种类');
				return
			}
			if(!this.provinceName){
				ut.totast('请选择服务范围');
				return
			}
			if(!this.name){
				ut.totast('请输入姓名');
				return
			}
			if(!this.sex){
				ut.totast('请选择性别');
				return
			}
			if(this.phone=='-1'){
				ut.totast('请输入手机号');
				return
			}
			if(this.type==1){
				this.req_addcraftsman(serviceClassIds)
			}
			if(this.type==2||this.type==5){
				this.req_addstore(serviceClassIds)
			}
			if(this.type==3){
				this.req_addexpress(serviceClassIds)
			}
			if(this.type==4){
				this.req_addcarry()
			}
			if(this.type==6){
				this.req_addjoin()
			}
			
		},
		return_serviceClassIds(){
			let serviceClassIds='';
			this.classlist.forEach(item=>{
				if(item.check){
					if(serviceClassIds){
						serviceClassIds+=','+item.id
					}else{
						serviceClassIds+=item.id
					}
				}
			})
			return serviceClassIds;
		},
		req_addstore(serviceClassIds){
			let url = "join/addstore";
			if(this.type==5){
				url='join/addcustomstore'
			}
				ut.request({
					data: {
						name:this.name,
						phone:this.phone,
						scope:this.provinceName+this.countyAreaName+this.cityName,
						serviceScope:this.provinceName+this.countyAreaName+this.cityName,
						serviceClassIds:serviceClassIds,
						sex:this.sex,
						adress:this.adress
					},
					c:true,
					url: url
				}).then(data=>{
					wx.showModal({
					title: '提示',
					content: '请保持电话畅通，木斗客服3个工作日内会和您联系，通知您来面试。',
					showCancel:false,
					success(res) {
						if (res.confirm) {
						wx.navigateBack();
						} 
					}
					})
				})
			
		},
		req_addcraftsman(serviceClassIds){
			ut.request({
				data: {
					name:this.name,
					phone:this.phone,
					scope:this.provinceName+this.countyAreaName+this.cityName,
					serviceClassIds:serviceClassIds,
					sex:this.sex
				},
				c:true,
				url: "join/addcraftsman"
			}).then(data=>{
				wx.showModal({
				title: '提示',
				content: '请保持电话畅通，木斗客服3个工作日内会和您联系，通知您来面试。',
				showCancel:false,
				success(res) {
					if (res.confirm) {
					wx.navigateBack();
					} 
				}
				})
			})
		},
		req_addexpress(serviceClassIds){
			ut.request({
				data: {
					name:this.name,
					phone:this.phone,
					scope:this.provinceName+this.countyAreaName+this.cityName,
					vehicleTypesIds:serviceClassIds,
					sex:this.sex
				},
				c:true,
				url: "join/addexpress"
			}).then(data=>{
				wx.showModal({
				title: '提示',
				content: '请保持电话畅通，木斗客服3个工作日内会和您联系，通知您来面试。',
				showCancel:false,
				success(res) {
					if (res.confirm) {
					wx.navigateBack();
					} 
				}
				})
			})
		},
		req_addcarry(){
			ut.request({
				data: {
					name:this.name,
					phone:this.phone,
					scope:this.provinceName+this.countyAreaName+this.cityName,
					sex:this.sex
				},
				c:true,
				url: "join/addcarry"
			}).then(data=>{
				wx.showModal({
				title: '提示',
				content: '请保持电话畅通，木斗客服3个工作日内会和您联系，通知您来面试。',
				showCancel:false,
				success(res) {
					if (res.confirm) {
					wx.navigateBack();
					} 
				}
				})
			})
		},
		req_addjoin(){
			ut.request({
				data: {
					name:this.name,
					phone:this.phone,
					scope:this.provinceName+this.countyAreaName+this.cityName,
					sex:this.sex
				},
				c:true,
				url: "join/addjoin"
			}).then(data=>{
				wx.showModal({
				title: '提示',
				content: '请保持电话畅通，木斗客服3个工作日内会和您联系，通知您来面试。',
				showCancel:false,
				success(res) {
					if (res.confirm) {
					wx.navigateBack();
					} 
				}
				})
			})
		}
	}
}
